import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { Videos } from '../videos';

declare var jQuery:any;

declare var FB:any;

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.css']
})
export class VideoComponent implements OnInit, AfterViewInit {

  mode: any;
  player: any;
  videos: any = Videos;
  currentVideo: any;
  nextVideo: any;
  url: any;

  constructor(private route: ActivatedRoute, private router: Router) {
      router.events.subscribe((val) => {
          this.url = 'domain.com/' + val.url;
          if (typeof FB !== 'undefined') this.changeDomain('test');
      });
  }

  ngOnInit() {
      this.route
      .params
      .subscribe(params => {
          this.mode = params['id'];
          this.videos.forEach((video, index) => {
              if (video.id == this.mode){
                  this.currentVideo = video;
                  if (index < this.videos.length - 1){
                      this.nextVideo = this.videos[index + 1];
                  }
                  else {
                      this.nextVideo = this.videos[0];
                  }
              }
          });
      });
  }

  ngAfterViewInit() {
    let temp = this;
    this.player = new window['MediaElementPlayer']('#video-player', {
      plugins: ['youtube'],
      playsinline: 'playsinline',
      'webkit-playsinline': 'webkit-playsinline',
      startLanguage: 'pt',
      success: (mediaElement, domObject) => {
        mediaElement.addEventListener('play', function(e) {
           temp.enterFullScreen();
       }, false);
      }
    });
    this.getFBComments(document, 'script', 'facebook-jssdk');
  }

  changeVideo(nextVideoId: string): void {
      let temp = this;
      this.mode = nextVideoId;

      if (this.player) this.player.remove();

      jQuery('video').attr('src', 'https://www.youtube.com/' + this.mode);
      jQuery('track').attr('src', 'assets/subtitles/' + this.mode + '.vtt');

      this.player = new window['MediaElementPlayer']('.video-player', {
        plugins: ['youtube'],
        playsinline: 'playsinline',
        'webkit-playsinline': 'webkit-playsinline',
        startLanguage: 'pt',
        success: (mediaElement, domObject) => {
          mediaElement.addEventListener('play', function(e) {
             temp.enterFullScreen();
         }, false);
        }
      });
  }

  enterFullScreen(): void {
    if (screen.width < 1000) this.player.enterFullScreen();
  }

  shareFacebook(): void {
    FB.ui({
      method: 'share',
      mobile_iframe: true,
      display: 'iframe',
      href: 'change',
    }, function(response){});
  }

  getFBComments(d, s, id): void {
    var js, fjs = d.getElementsByTagName(s)[0];
    if (d.getElementById(id)) return;
    js = d.createElement(s); js.id = id;

    js.src = "//connect.facebook.net/pt_BR/sdk.js#xfbml=1&version=v2.8";
    fjs.parentNode.insertBefore(js, fjs);
  }

  changeDomain(value): void {
      var newVal = '<fb:comments href="http://domain.com/' + this.url + '" num_posts="2" width="100%"></fb:comments><div id="fb-root"></div>';
      jQuery('.fb-comments').html(newVal);
      FB.XFBML.parse(jQuery('.fb-comments').get(0),function(){
          jQuery(".FB_Loader").remove();
      });
  }

  removeFBComments(id): void {
      let fjs = document.getElementsByTagName('script')[0];
      let elementToRemove = document.getElementById(id);
      if (elementToRemove) fjs.parentNode.removeChild(elementToRemove);
  }

}



